.help {
  position: relative;
  top:3vh;
  left:200vh;
  font-size: 17px;
  font-weight: 800;
  color: #c77dff;
  padding: 1em 2em;
  border: none;
  background: none;
  clip-path: polygon(0 0, 67% 0, 100% 100%, 33% 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.3s 0.3s;
}

.help:hover {
  text-shadow: 1px 1px 20px #e0aaff, 1px 1px 60px #e0aaff;
}

.help:active {
  transform: scale(0.89);
}

.help::before,
.help::after {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  background: #c77dff;
  transition: width 0.3s 0.6s;
}

.help::before {
  top: 0;
  left: 0;
  transform-origin: top;
}

.help::after {
  bottom: 0;
  right: 0;
  transform-origin: bottom;
}

.help:hover::before,
.help:hover::after {
  width: 65%;
}

.help span {
  position: absolute;
  width: 0;
  height: 2px;
  background: #c77dff;
  transition: width 0.3s, transform 0.3s 0.3s;
  transform: rotate(22.3deg);
}

.help span:nth-child(1) {
  top: 0;
  left: 0;
  transform-origin: left;
}

.help span:nth-child(2) {
  bottom: 0;
  right: 0;
  transform-origin: right;
}

.help:hover span {
  width: 55%;
  transform: rotate(50deg);
}



